function sliderGallery() {
    var swiper = new Swiper('.gallery .swiper', {
        slidesPerView: 3,
        spaceBetween: 20,
        navigation: {
            nextEl: '.gallery .swiper-button-next',
            prevEl: '.gallery .swiper-button-prev',
        },
        pagination: {
            el: ".gallery .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            577: {
                slidesPerView: 2,
            },
            993: {
                slidesPerView: 3,
            }
        }
    })
}

function sliderReviews() {
    var swiper = new Swiper('.reviews .swiper', {
        slidesPerView: 3,
        spaceBetween: 20,
        navigation: {
            nextEl: '.reviews .swiper-button-next',
            prevEl: '.reviews .swiper-button-prev',
        },
        pagination: {
            el: ".reviews .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            577: {
                slidesPerView: 2,
            },
            993: {
                slidesPerView: 3,
            }
        }
    })
}

$(document).ready(function() {
    sliderGallery();
    sliderReviews();


//Burger menu animation with greensock / gsap
    var menuBtn = $(".menu-btn"),
        menuitem1 = $(".menu__item--1"),
        menuitem2 = $(".menu__item--2"),
        menuitem3 = $(".menu__item--3"),
        speed = 0.15;

//instantiate  timeline
    var tl = new TimelineLite({paused:true}); //pause default

//collapse menu
    tl.to(menuitem1, speed, {top: 15, ease:Quint.easeOut}, "label--1")
        .to(menuitem3, speed, {top: -15, ease:Quint.easeOut}, "label--1")

        //rotate all items
        .to([menuitem1, menuitem2, menuitem3], speed, {rotation: -90, ease:Quint.easeOut})

        //expand menu
        .to(menuitem1, speed, {left: 20, ease:Quint.easeOut}, "label--2")
        .to(menuitem3, speed, {right: 20, ease:Quint.easeOut}, "label--2");


// play timeline on click, reverse animation if active
    menuBtn.click(function() {
        $(this).toggleClass('active');
        $('.header').toggleClass('header--active')
        $('body').toggleClass('fixed-body')

        if ( $(this).hasClass("active") ) {
            tl.play();
        }
        else {
            tl.reverse();
        }
    });


    // Modal
    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })


    function closeModal() {
        $('.modal').removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }

})

// Animation
const animItems = document.querySelectorAll('._anim-items')

if (animItems.length > 0) {
    window.addEventListener('scroll', animOnScroll)
    function animOnScroll(params) {
        for (let index = 0; index < animItems.length; index++) {
            const animItem = animItems[index];
            const animItemHeight = animItem.offsetHeight;
            const animItemOffset = offset(animItem).top;
            const animStart = 4;

            let animItemPoint = window.innerHeight - animItemHeight / animStart;

            if (animItemHeight > window.innerHeight) {
                animItemPoint = window.innerHeight - window.innerHeight / animStart;
            }

            if ((pageYOffset > animItemOffset - animItemPoint) && pageYOffset < (animItemOffset + animItemHeight)) {
                animItem.classList.add('active');
            } else {
                if (!animItem.classList.contains('anim-no-hide')) {
                    animItem.classList.remove('active')
                }
            }
        }
    }
    function offset(el) {
        const rect = el.getBoundingClientRect(),
            scrollLeft = window.pageXOffset || document.documentElement.scrollLeft,
            scrollTop = window.pageYOffset || document.documentElement.scrollTop;
        return { top: rect.top + scrollTop, left: rect.left + scrollLeft }
    }

    setTimeout(() => {
        animOnScroll();
    }, 300)
}

